import React, { useState } from "react";
import { authService, firebaseInstacne } from "fbase";
import AuthForm from "components/AuthForm";

const Auth = () => {
    const onSolcialClick = async (event) => {
        const {
            target: {name},
        } = event;
        let provider;
        if(name === "google") {
            provider = new firebaseInstacne.auth.GoogleAuthProvider();
        } else if(name === "github") {
            provider = new firebaseInstacne.auth.GithubAuthProvider();
        }
        const data = await authService.signInWithPopup(provider);
        console.log(data);
    }
    return (
        <div>
            <AuthForm />
            <div>
                <button onClick={onSolcialClick} name="google">Continue with Google</button>
                <button onClick={onSolcialClick} name="github">Continue with Github</button>
            </div>
        </div>        
    );
};

export default Auth;